import * as React from 'react';

import EmailIcon from '@mui/icons-material/Email';
import GitHubIcon from '@mui/icons-material/GitHub';
import Avatar from '@mui/material/Avatar';


import './headerNav.scss'

const HeaderNav = () => {
    return (
        <div className="headerNav">
            <div className="container">
                <div className="headerNav__text">
                    
                </div>

                <div className="headerNav__icons">
                    <div>
                        <a href="">
                            <EmailIcon className='headerNav__icons-email'/>
                        </a>
                        <a href="">
                            <GitHubIcon className='headerNav__icons-git'/>
                        </a>
                    </div>
                    
                    
                    <Avatar className='headerNav__icons-avatar' alt="Anton Vladislavskiy" src="/static/images/avatar/1.jpg"/>
                </div>

            </div>
        </div>
    )
}

export default HeaderNav