import LocationOnIcon from '@mui/icons-material/LocationOn';
import SchoolIcon from '@mui/icons-material/School';

import Banner from './Banner';
import Avatar from './Avatar';

import './profile.scss'
import MoreDetailedButton from './MoreDetailetButton';





const Profile = () => {

    const otherInfo = [
        {title: '', icon: <LocationOnIcon/>, class: 'location'},
        {title: ' . ', icon: <SchoolIcon/>, class: 'education'},
       
    ]

    const otherInfoBlock = otherInfo.map((otherInfo, index) => {
        return (
            <div className={otherInfo.class} key={index}>
                {otherInfo.icon}
                <span>{otherInfo.title}</span>
            </div>
        )
    })

    return (
        <div className="profile">
            <div className="container">
                <Banner/>
                <div className="profile__content">
                    <Avatar/>
                    <div className="profile__content-info">
                        <span className='name-info'> </span><br/>
                        <div className="other-info" >
                            {otherInfoBlock}
                            <MoreDetailedButton/>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Profile