import React from 'react'
import './projects.scss'

const Projects = () =>  {

  const infoProjects =  [
    {
    url: 'https://antonfrez.github.io/PokeCard/',
    title: 'Poke Api',
    descr: ' -    '
    },
    {
    url: 'https://antonfrez.github.io/Marvel/',
    title: 'Marvel information portal',
    descr: '     Marvel' 
    },
    {
    url: 'https://antonfrez.github.io/Adding-to-cart/',
    title: 'Sushi Store',
    descr: '   ' 
    },
]

const projectsBlock = infoProjects.map((infoProject, index) => {
  return (
      <div className="projects__item">
          <a href={infoProject.url}>
              <div className={`projects__box projects__box-${index}`} key={index}>
                  <div className="overlay">
                      <div className="projects__item-title">
                              <p>{infoProject.title}</p>
                      </div>

                      <div className="projects__item-descr">
                          <p>{infoProject.descr}</p>
                      </div>
                  </div>
              </div>
          </a>
      </div>
      
  );
})

  return (
    <div className='projects'>
      <span></span>
{projectsBlock}
      
    </div>
  )
}

export default Projects