import React from 'react'
import BEM from '../assets/icons/bem.svg'
import html from '../assets/icons/icons8-html-5.svg'
import css3 from '../assets/icons/css3-svgrepo-com.svg'
import js from '../assets/icons/icons8-javascript.svg'
import react from '../assets/icons/icons8-react-native.svg'
import node from '../assets/icons/icons8-nodejs.svg'
import sass from '../assets/icons/icons8-sass.svg'
import git from '../assets/icons/icons8-git.svg'
import Bootstrap from '../assets/icons/bootstrap.svg'

import './skills.scss'



const Skills = () => {

  const infoSkills =  [
    {name:`HTML5`, img: html},
    {name:`CSS3`, img: css3 },
    {name: `JavaScript`, img: js},
    {name: `React`, img: react},
    {name: `Node.js`, img: node},
    {name: `Sass`, img: sass},
    {name: ``, img: BEM},
    {name: `Git`, img: git},
    {name: `Bootstrap`, img: Bootstrap},
]

const skillsBlock = infoSkills.map((infoSkill, index) => {
  return (
      <div className='skills__item' key={index}>
          <div className="skills__item-el">
              <img alt='' className="skills__item-img" src={infoSkill.img}></img>
              <h4 className="skills__item-title">{infoSkill.name}</h4>
          </div>
      </div>
  );
})

  return (
    <div className='skills'>
      <span></span>
      <div className='container'>
      
          {skillsBlock}
      </div>
    </div>
  )
}

export default Skills
