import React from 'react'
import rusbitech from '../assets/rusbitech.jpg'
import kameleoon from '../assets/kameleoon.jpg'
import './experience.scss'


const Experience = () => {

  const workData = [
    {nameCompany: ``, namePost: `-`, icon: rusbitech, date: ` 2023 -  `, descr: `    ExtJS   .`,},
    {nameCompany: `Kameleoon`, namePost: `JavaScript `, icon: kameleoon, date: ` 2021   2022 (1   6 )`, descr: ` -  A/B    JavaScript,    , .`, namePost2: ``, date2: ` 2020   2021 (1   3 )`, descr2: `  -     .`},
  ]

  const workDataBlock = workData.map((workData, index) => {
    return (
      <div class="card">
        <div class="card__info">
          <div className="card__info-header">
            <img className='work-img' src={workData.icon}></img>
            <h2 class="title">{workData.nameCompany}</h2>
          </div>
     
        <div className="info-body">
          <div className="prof">
            <h3 className='profession-js'>{workData.namePost}</h3>
            <h4>{workData.date} </h4>
            <p>{workData.descr}</p>

            <h3 className='profession-qa'>{workData.namePost2}</h3>
            <h4>{workData.date2}</h4>
            <p>{workData.descr2}</p>
          </div>
        </div>
        </div>
      </div>
    )
}) 

  return (
    <div className='experience'>
      <span> </span>
      {workDataBlock}
    </div>
  )
}

export default Experience