import './example.scss'
import email from '../assets/icons/email.svg'
import github from '../assets/icons/github.svg'

const Example = () => {

    const contactsInfo = [
        {name: `aovladislavskiy@mail.ru`, url: `mailto:aovladislavskiy@mail.ru`, icon: email},
        {name: `GitHub.com`, url: `https://github.com/AntonFrez`, icon: github}
    ]

    const contactsBlock = contactsInfo.map((contactsInfo, index) => {
        return (
            <ul>
                <li>
                    <a href={contactsInfo.url} target="blank" rel="noopener">
                        <div>
                            <img src={contactsInfo.icon} alt='img'/>
                        </div>
                        <div>{contactsInfo.name}</div>
                    </a>
                </li>
            </ul>
        )
    })

    return (
            <div className="contacts">
                <span></span>
                <div className="container">
                    {contactsBlock}
                </div>
            </div>
        )
    }
    

export default Example